body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.app-container {
  text-align: center;
  width: 100%;
  max-width: 400px;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.toggle-button {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.toggle-button:hover {
  background: #0056b3;
}

.container {
  text-align: left;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form label {
  font-weight: bold;
}

.form input,
.form select {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.button-group {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button[type="submit"] {
  background: green;
  color: white;
}

button[type="reset"] {
  background: red;
  color: white;
}

.profile-page {
  text-align: center;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.user-info {
  background: #f9f9f9;
  padding: 10px;
  border-radius: 5px;
}

.user-posts {
  background: #f1f1f1;
  padding: 10px;
  border-radius: 5px;
}

.user-posts ul {
  list-style: none;
  padding: 0;
}

.user-posts li {
  background: white;
  padding: 8px;
  margin-top: 5px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}